<template>
                    
<div class="datagrid" :class="cssClass">

  <template>

  </template>

  <div class="grid-wrapper">

    <div class="grid-header">
      <input class="input is-rounded is-small quicksearch" type="text" placeholder="search" v-model="listparams.q">
      <div class="spacer"></div>
      <div class="buttons field has-addons">
        <button class="button is-small " v-on:click="sizeToFit()" title="Size to Fit"><i class="fas fa-arrows-alt-h"></i>&nbsp;<span>fit</span></button>
        <button class="button is-small " v-on:click="autoSizeAll(false)" title="Auto-Size All"><i class="fal fa-text-width"></i>&nbsp;<span>headers</span></button>
        <button class="button is-small " v-on:click="autoSizeAll(true)" title="Auto-Size All (Skip Header)"><i class="fal fa-text-width"></i>&nbsp;<span>data</span></button>
        <button class="button is-small" v-on:click="expandAll()" title="Expand / Collapse Groups" v-if="isGrouped"><i class="fal fa-expand-alt"></i>&nbsp;<span>groups</span></button>
      </div>
      <template>

      </template>

      <div class="grid-paginator" v-if="paginationTotalPages>1">

        {{paginationCurrentPage}} /
        {{paginationTotalPages}} <a href="#" @click.prevent="prevPage">
          <b-icon pack="fas" icon="chevron-left">
          </b-icon>
        </a>

        <a href="#" @click.prevent="nextPage">
          <b-icon pack="fas" icon="chevron-right">
          </b-icon>
        </a>

      </div>
      <div class="list-reloader">
        <b-button type="is-text" @click.prevent="getItems" :loading="isLoading">
          <b-icon pack="fas" icon="redo">
          </b-icon>
        </b-button>
        <!-- 
        <b-button
          type="is-text"
          @click.prevent="saveState"
          :loading="isLoading"
        >
          <b-icon
            pack="fas"
            icon="save"
          >
          </b-icon>
        </b-button>

        <b-button
          type="is-text"
          @click.prevent="restoreState"
          :loading="isLoading"
        >
          <b-icon
            pack="fas"
            icon="rocket"
          >
          </b-icon>
        </b-button> -->
      </div>
    </div>

    <ag-grid-vue :style="styles" class="ag-theme-balham" :column-defs="columnDefs" :row-data="rowData" :grid-options="gridOptions" @row-data-changed="onRowDataChanged" @row-value-changed="updateItem" @row-editing-started="prepareItemForEditing" @on-filter-changed="onFilterChanged" @column-row-group-changed="onColumnRowGroupChanged" @pagination-changed="onPaginationChanged" @on-cell-editing-started="onCellEditingStarted" @sort-changed="onGridStateChanged" @filter-changed="onGridStateChanged" @column-visible="onGridStateChanged" @drag-stopped="onGridStateChanged" @column-resized="onGridStateChanged" @grid-ready="onGridReady" @first-data-rendered="onFirstDataRendered" :pagination-page-size="paginationPageSize" :pagination="pagination" :row-group-panel-show="rowGroupPanelShow" :get-context-menu-items="getContextMenuItems" :status-bar="statusBar" :enable-range-selection="true" :quick-filter-text="listparams.q" :group-default-expanded="groupDefaultExpanded">
    </ag-grid-vue>
    <ag-grid-vue v-if="showTotals" style="width: 100%; height: 30px;overflow:hidden" class="ag-theme-balham" :grid-options="bottomGridOptions" :header-height="0" :column-defs="columnDefs" :row-data="totalsData" :row-style="bottomRowStyle"></ag-grid-vue>

  </div>
  <template></template>

</div>

                  </template> 
                  <script >
import { AgGridVue } from "ag-grid-vue";
import DistinctValueChooser from "./DistinctValueChooser.vue";
import GermanDateEditor from "./GermanDateEditor.vue";
import DropDownEditor from "./DropDownEditor.vue";

import {
  handleErrorsFromResponse,
  handleNotificationsFromResponse,
  displayNotifications,
  array_get
} from "../utils.js";

import axios from "axios";
const R = require("ramda");

var newItemCount = 0;

const my_range = function(start, stop) {
  const step = stop > start ? 1 : -1;
  stop = stop + step;
  return Array(Math.ceil((stop - start) / step))
    .fill(start)
    .map((x, y) => x + y * step);
};

export default {
  name: "BaseList",
  // params: {
  //   table: { required: true }
  // },
  data() {
    return {
      cssClass: "",
      table: null,
      sourceColumns: null,
      colvalues: {},
      disableEditableFieldProtection: false,
      allowAdd: false,
      allowDelete: false,
      allowDuplicate: false,
      listparams: {},
      columnApi: null,
      columnDefs: null,
      initialColumnDefs: null,
      extendColumnDefs: null,
      nameOfIdColumn: "id",
      showTotals: false,
      gridApi: null,
      gridColumnApi: null,
      rowData: null,
      totalsData: [],
      statusBar: null,
      rowGroupPanelShow: null,
      firstDataHasBeenRendered: false,
      groupDefaultExpanded: -1,
      styles: {
        width: "100%"
      },
      gridState: {},
      baseDefaultColDef: {
        sortable: true,
        filter: true,
        editable: false
      },
      isLoading: false,
      getItemsStarted: false,
      gridOptions: null,
      defaultSortModel: null,
      bottomGridOptions: {
        alignedGrids: []
      },
      bottomRowStyle: { fontWeight: "bold" },
      paginationLastPageFound: null,
      paginationCurrentPage: null,
      paginationTotalPages: 0,
      paginationPageSize: 99,
      pagination: true,
      expanded: false,
      isGrouped: false,
      defaultGridOptions: {
        domLayout: "autoHeight",
        alignedGrids: [],
        defaultColDef: {
          resizable: true,
          sortable: true,
          filter: true
        },
        suppressClickEdit: true,
        editType: "fullRow",
        columnTypes: {
          integerColumn: {},
          dropDownColumn: {
            valueFormatter: this.dropDownFormatter,
            cellEditorFramework: "DropDownEditor"
          },
          germanDateColumn: {
            filter: "agDateColumnFilter",
            filterParams: {
              // provide comparator function
              comparator: function(filterLocalDateAtMidnight, cellValue) {
                var dateAsString = cellValue;
                if (dateAsString == null) return 0;

                // In the example application, dates are stored as YYYY-MM-DD
                // We create a Date object for comparison against the filter date
                var dateParts = dateAsString.split("-");
                var day = Number(dateParts[2]);
                var month = Number(dateParts[1]) - 1;
                var year = Number(dateParts[0]);
                var cellDate = new Date(day, month, year);

                // Now that both parameters are Date objects, we can compare
                if (cellDate < filterLocalDateAtMidnight) {
                  return -1;
                } else if (cellDate > filterLocalDateAtMidnight) {
                  return 1;
                } else {
                  return 0;
                }
              }
            },
            valueFormatter: this.germanDateFormatter,
            cellEditorFramework: "GermanDateEditor"
          }
        }
        // statusBar: {
        //   statusPanels: [
        //     {
        //       statusPanel: "agAggregationComponent",
        //       statusPanelParams: {
        //         // possible values are: 'count', 'sum', 'min', 'max', 'avg'
        //         aggFuncs: ["count", "sum", "min", "max", "avg"]
        //       }
        //     }
        //   ]
        // }
      }
    };
  },
  components: {
    AgGridVue,
    DistinctValueChooser,
    GermanDateEditor,
    DropDownEditor
  },
  computed: {
    agDataSource: function() {
      return {
        getRows: this.getRows
      };
    },
    editableFieldnames: function() {
      return R.pipe(
        R.filter(c => c.editable),
        R.map(c => c.field)
      )(this.columnDefs);
    },
    defaultColDef: function() {
      return this.baseDefaultColDef;
    }
  },
  methods: {
    onGridReady: function() {
      console.log("#log 9529 gridReady");
      // this.logColIds();
    },
    setIsGrouped: function() {
      this.isGrouped = !this.gridApi.columnController.isRowGroupEmpty();
      if (this.paginationPageSize == 1) {
        if (!this.isGrouped) {
          this.gridApi.paginationSetPageSize(100);
        } else {
          // this.gridApi.paginationSetPageSize(1);
        }
      }
    },
    sizeToFit() {
      this.gridApi.sizeColumnsToFit();
    },
    expandAll() {
      if (!this.expanded) {
        this.gridApi.expandAll();
        this.expanded = true;
      } else {
        this.gridApi.collapseAll();
        this.expanded = false;
      }
    },
    autoSizeAll(skipHeader) {
      var allColumnIds = [];
      this.gridColumnApi.getAllColumns().forEach(function(column) {
        allColumnIds.push(column.colId);
      });
      this.gridColumnApi.autoSizeColumns(allColumnIds, skipHeader);
    },
    logColIds: function() {
      var cols = this.gridColumnApi.getAllColumns();
      cols.forEach(col => {
        var colDef = col.getUserProvidedColDef();
        console.log(
          "#log 8711" +
            this.table +
            "_" +
            this.listparams.dataview +
            "," +
            colDef.headerName +
            ", Column ID = " +
            col.getId(),
          colDef
        );
      });
    },
    onFirstDataRendered: function() {
      // console.log("#log 4980", "onFirstDataRendered");
      if (!this.firstDataHasBeenRendered) {
        this.restoreGridStateFromLocalStorage(
          this.table + "_" + this.listparams.dataview
        );
      }
      this.firstDataHasBeenRendered = true;
      this.setIsGrouped();
    },
    onGridStateChanged: function() {
      if (
        this.defaultSortModel &&
        this.defaultSortModel.length > 0 &&
        this.gridApi.getSortModel().length == 0 &&
        this.gridColumnApi.getAllColumns()
      ) {
        console.log("#log 5841 setSortModel", this.defaultSortModel);
        this.gridApi.setSortModel(this.defaultSortModel);
        if (this.gridApi.getSortModel().length == 0) {
          console.error(
            '#sortModel was zero-length after setting "defaultSortModel"',
            this.defaultSortModel
          );
          // this.defaultSortModel = null;
        }
      }
      console.log(
        "#log 7179 onGridStateChanged",
        this.table + "_" + this.listparams.dataview,
        this.gridApi.getSortModel()
      );
      // this.logColIds();

      if (this.firstDataHasBeenRendered) {
        this.saveGridStateToLocalStorage(
          this.table + "_" + this.listparams.dataview
        );
      }
    },
    saveGridStateToLocalStorage: function(key) {
      console.log("#log 4171", "saveGridStateToLocalStorage");
      this.gridState = this.getGridState();

      localStorage.setItem("grid_state-" + key, JSON.stringify(this.gridState));
    },
    restoreGridStateFromLocalStorage: function(key) {
      const stateAsJson = localStorage.getItem("grid_state-" + key);
      console.log("#log 4189", "restoreGridStateFromLocalStorage");

      if (stateAsJson) {
        const state = JSON.parse(stateAsJson);
        this.gridState = state;
        if (state) {
          this.gridColumnApi.setColumnState(state.col);
          // this.gridColumnApi.setColumnGroupState(state.group);
          this.gridApi.setSortModel(state.sort);
          this.gridApi.setFilterModel(state.filter);
        }
      }
    },
    storeSourceColumns: function(selectedSourceColumns) {
      this.sourceColumns = selectedSourceColumns;
    },
    getContextMenuItems: function(params) {
      const selectedSourceColumns = this.getSelectedSourceColumns();

      let items = [...params.defaultItems];
      if (selectedSourceColumns) {
        const selectedSourceColumnsStr = selectedSourceColumns.colnames.join(
          ", "
        );
        items = [
          {
            name: `copy '${selectedSourceColumnsStr}'`,
            action: () => {
              this.storeSourceColumns(selectedSourceColumns);
            }
          },
          ...items
        ];
      }

      if (this.sourceColumns) {
        if (this.gridApi.getCellRanges().length > 0) {
          // target ranges present ?
          const sourceColumnsStr = this.sourceColumns.colnames.join(", ");
          items = [
            {
              name: `paste '${sourceColumnsStr}' to this rows`,
              action: () => {
                this.pasteToSelectedRanges();
              }
            },
            ...items
          ];
        }
      }
      if (this.allowAdd) {
        items = [
          {
            name: `duplicate row`,
            action: () => {
              this.duplicateItem(params.node.rowIndex);
            }
          },
          ...items
        ];
      }
      if (this.allowDelete) {
        items = [
          {
            name: `delete row ${params.node.rowIndex}`,
            action: () => {
              this.removeItem(params.node.rowIndex);
            }
          },
          ...items
        ];
        if (
          this.allowDelete == "multiple" &&
          this.gridApi.getCellRanges().length > 0
        ) {
          let target_ids = this.getSelectedRowIds();
          if (target_ids.length > 1) {
            items = [
              {
                name: `delete ${target_ids.length} selected rows`,
                action: () => {
                  this.removeItems(target_ids);
                }
              },
              ...items
            ];
          }
        }
      }

      return items;
    },

    pasteToSelectedRanges: function() {
      //this.sourceColumns
      //loop over all rows:
      let target_ids = this.getSelectedRowIds();

      const data = {
        ...this.sourceColumns,
        target_ids
      };
      axios
        .post(`/api/ag-grid/paste/${this.table}`, data)
        .then(res => {
          this.getItems();
          displayNotifications(this, [
            { type: "toast", class: "is-success", text: "✔ data pasted" }
          ]);
        })
        .catch(error => {
          console.error(error);
          handleErrorsFromResponse(this, error.response);
        });
    },
    getIdsOfRowsInRange: function(range) {
      return R.map(
        idx =>
          array_get(this.gridApi.getDisplayedRowAtIndex(idx), "data.id", null),
        my_range(range.startRow.rowIndex, range.endRow.rowIndex)
      );
    },
    prevPage: function() {
      this.gridApi.paginationGoToPreviousPage();
    },
    nextPage: function() {
      this.gridApi.paginationGoToNextPage();
    },
    onQuickFilterChanged: function(event) {
      this.gridApi.setQuickFilter(event.target.value);
    },
    createGridOptions: function() {
      return this.createBaseGridOptions();
    },
    setColDefs: function(colDefs) {
      //to ovveride
      this.baseSetColDefs(colDefs);
    },
    numberFormatter: function(field) {
      return this.baseNumberFormatter(field);
    },
    baseNumberFormatter: function(field) {
      return this.$options.filters.currency(field.value);
    },
    germanDateFormatter: function(field) {
      return this.baseGermanDateFormatter(field);
    },
    baseGermanDateFormatter: function(field) {
      return this.$options.filters.german_date(field.value);
    },
    dropDownFormatter: function(field) {
      let vals = this.getColvaluesForField(field.colDef.field);
      let val = vals.find(i => i[0] === field.value);
      if (val) {
        return val[1];
      } else {
        return field.value;
      }
    },
    baseSetColDefs: function(colDefs) {
      const newdefs = R.map(col => {
        if (col.type == "numericColumn" && !col.valueFormatter) {
          col.valueFormatter = this.numberFormatter;
        }

        if (col.renderer) {
          col.cellRenderer = arg => window[col.renderer](arg);
        }

        if (col.formatter) {
          col.valueFormatter = arg => window[col.formatter](arg);
        }

        return col;
      }, colDefs);

      this.columnDefs = newdefs;
      console.log("#log 9796 baseSetColDefs", this.columnDefs);
    },
    makeEditable: function() {
      this.defaultGridOptions.suppressClickEdit = false;
      this.defaultGridOptions.enableCellTextSelection = false;
    },
    createBaseGridOptions: function() {
      return this.defaultGridOptions;
    },
    createColumnDefs: function(coldefs, listOfMinimalColnames = []) {
      const minimalColDefs = R.map(field => {
        return { field };
      })(listOfMinimalColnames);

      coldefs = R.reduce((acc, minimalColdef) => {
        if (!R.find(R.propEq("field", minimalColdef.field))(acc)) {
          return [...acc, minimalColdef];
        }
        return acc;
      }, coldefs)(minimalColDefs);
      let ret = R.map(coldef => {
        //assign default-coldef to passed columns
        return { ...this.defaultColDef, ...coldef };
      })(coldefs);
      return ret;
    },
    getRowDataAtIndex: function(idx) {
      return array_get(this.gridApi.getDisplayedRowAtIndex(idx), "data", {});
    },
    removeItems: function(target_ids) {
      if (confirm(`wirklich ${target_ids.length} Einträge löschen ?`)) {
        R.map(id => {
          axios
            .delete(`/api/ag-grid/${this.table}/${id}`)
            .then(res => {
              const idx = this.rowData.findIndex(r => r.id == id);
              this.rowData.splice(idx, 1);
              displayNotifications(this, [
                { type: "toast", class: "is-success", text: "✔ row deleted" }
              ]);
            })
            .catch(error => {
              console.error(error);
              handleErrorsFromResponse(this, error.response);
            });
        })(target_ids);
      }
    },
    removeItem: function(rowIdx) {
      const row = this.getRowDataAtIndex(rowIdx);
      const id = array_get(row, "id", null);
      if (id && confirm("wirklich löschen ?")) {
        const idx = this.rowData.findIndex(r => r.id == row.id);

        if (idx >= 0) {
          axios
            .delete(`/api/ag-grid/${this.table}/${id}`)
            .then(res => {
              this.rowData.splice(idx, 1);
              displayNotifications(this, [
                { type: "toast", class: "is-success", text: "✔ row deleted" }
              ]);
            })
            .catch(error => {
              console.error(error);
              handleErrorsFromResponse(this, error.response);
            });
        }
      }
    },
    addItem: function(sourceRowIdx = 0) {
      const newrow = {
        name: `New Item ${newItemCount}`
      };

      this.gridApi.setFilterModel(null);
      this.gridApi.onFilterChanged();
      this.gridApi.setSortModel(null);
      this.gridApi.onSortChanged();

      this.gridApi.updateRowData({ add: [newrow], addIndex: 0 });
      this.$nextTick(() => {
        this.gridApi.startEditingCell({
          rowIndex: 0,
          colKey: "name"
        });
      });
      newItemCount++;
    },
    getTempId: function() {
      return (
        Math.random()
          .toString(36)
          .substring(2, 15) +
        Math.random()
          .toString(36)
          .substring(2, 15)
      );
    },
    duplicateItem: function(sourceIdx) {
      const _temp_id = this.getTempId();
      const rowdata = this.getRowDataAtIndex(sourceIdx);
      const newrow = { ...rowdata, _temp_id };
      this.gridApi.updateRowData({ add: [newrow], addIndex: sourceIdx + 1 });
      let rowIndex = -1;
      this.gridApi.forEachNodeAfterFilterAndSort(function(rowNode, index) {
        if (rowNode.data._temp_id == _temp_id) {
          rowIndex = index;
        }
      });
      if (rowIndex >= 0) {
        this.$nextTick(() => {
          this.gridApi.startEditingCell({
            rowIndex,
            colKey: "name"
          });
        });
      }
    },
    callApi: function(url0, data, method = "POST") {
      const url = "/api" + url0;
      console.log(`➜ ➜  calling API ${method} ${url}`, data);
      return new Promise((resolve, reject) => {
        axios
          .request({ url, method, data })
          .then(response => {
            console.log(`← ← API returned ${method} ${url}`, response.data);

            handleNotificationsFromResponse(this, response);
            resolve(response.data.valid);
          })
          .catch(error => {
            console.error(`← ←  API failed ${method} ${url}`, error);

            handleErrorsFromResponse(this, error.response);
            reject(error.response);
          });
      });
    },
    getSelectedSourceColumns: function() {
      const ranges = this.gridApi.getCellRanges();

      if (
        ranges.length == 1 && // only 1 range selected
        ranges[0].startRow.rowIndex == ranges[0].endRow.rowIndex // only 1 row selected
      ) {
        const columns = ranges[0].columns;
        const colnames = R.pipe(
          R.filter(col => col.colDef.editable == true), // filter only editable cols
          R.map(col => col.colId)
        )(columns);

        if (colnames.length > 0) {
          // found at least one editable col

          //find column id:

          const id = array_get(
            this.gridApi.getDisplayedRowAtIndex(ranges[0].startRow.rowIndex),
            "data.id"
          );

          return { id, colnames };
        }
      }
    },
    getSelectedRowIds: function() {
      return R.pipe(
        R.map(range => this.getIdsOfRowsInRange(range)),
        R.flatten
      )(this.gridApi.getCellRanges());
    },

    prepareItemForEditing: function(params) {
      //to override
    },

    updateItem: function(params) {
      let data = params.data;

      if (data._temp_id) {
        data = R.omit(["id", "_temp_id"], data);
      }

      console.log("#log 1045", params);
      const rowIdx = parseInt(params.node.id, 10);
      console.log("#updateItem", data, rowIdx);
      let id = array_get(data, this.nameOfIdColumn, null);
      let method = "PUT";
      let url = `/api/ag-grid/${this.table}/${id}`;

      let editableData = null;
      if (this.disableEditableFieldProtection) {
        editableData = data;
      } else {
        editableData = R.reduce((acc, fieldname) => {
          if (data.hasOwnProperty(fieldname)) {
            return { ...acc, [fieldname]: data[fieldname] };
          }
          return acc;
        }, {})([...this.editableFieldnames, "id"]);
      }
      if (id === null) {
        method = "POST";
        url = `/api/ag-grid/${this.table}`;
      }
      axios
        .request({
          url,
          method,
          data: { ...this.listparams, rowdata: editableData }
        })
        .then(res => {
          if (id === null) {
            id = array_get(res.data.payload, this.nameOfIdColumn, null);
          }
          if (id === null) {
            displayNotifications(this, [
              {
                type: "toast",
                class: "is-danger",
                text: "⚠ id of new record cannot be determined"
              }
            ]);
          } else {
            this.reloadRow(id, rowIdx);
          }

          displayNotifications(this, [
            { type: "toast", class: "is-success", text: "✔ data saved" }
          ]);
        })
        .catch(error => {
          console.error(error);
          handleErrorsFromResponse(this, error.response);
          if (id !== null) {
            this.reloadRow(id, rowIdx);
          }
        });
    },
    reloadRow: function(id, rowIdx) {
      this.getDataForSingleRow(id).then(row => {
        console.log("#reloadRow got Data", row);
        this.rowData.splice(rowIdx, 1, row);
        // this.resizeColumns(params);
      });
    },
    getDataForSingleRow: function(id) {
      return this.getItems(id);
    },
    getColvaluesForField: function(fieldname) {
      return array_get(this.colvalues, fieldname, []);
    },
    getColValueFields: function() {
      let ret = R.pipe(
        R.filter(
          cd =>
            array_get(cd, "cellEditorFramework") == "DistinctValueChooser" ||
            array_get(cd, "type") == "dropDownColumn"
        ),
        R.map(cd => cd.field)
      )(this.columnDefs || this.initialColumnDefs || []);
      // console.log(`#log 4124`, this.columnDefs, this.initialColumnDefs);
      return ret;
    },
    getGridState: function() {
      return {
        col: this.gridColumnApi.getColumnState(),
        group: this.gridColumnApi.getColumnGroupState(),
        sort: this.gridApi.getSortModel(),
        filter: this.gridApi.getFilterModel()
      };
    },
    getItems: function(id = null) {
      this.getItemsStarted = true;
      this.isLoading = true;

      let params = this.listparams;
      let colvaluefields = this.getColValueFields();
      if (colvaluefields) {
        params = { ...params, colvalues: R.join(",", colvaluefields) };
      }

      const record_id = Number.isInteger(id) && id > 0 ? id : null;

      if (record_id !== null) {
        params = { ...params, [this.nameOfIdColumn]: record_id };
      }

      console.log("#getITEMS", record_id);

      const promise = new Promise((resolve, reject) => {
        axios
          .get(`/api/ag-grid/${this.table}`, { params })
          .then(response => {
            const count = array_get(response, "data.payload.entries", [])
              .length;
            console.log("#getItems got response", count);
            this.isLoading = false;
            if (record_id === null) {
              this.rowData = response.data.payload.entries;
              this.colvalues = array_get(response.data.payload, "colvalues");
              if (!this.firstDataHasBeenRendered) {
                if (response.data.payload.col_defs) {
                  let localColDefs = response.data.payload.col_defs;
                  if (this.initialColumnDefs) {
                    localColDefs = this.mergeColDefs(
                      this.initialColumnDefs,
                      localColDefs
                    );
                  }
                  if (this.extendColumnDefs) {
                    localColDefs = this.mergeColDefs(
                      localColDefs,
                      this.extendColumnDefs
                    );
                  }
                  this.setColDefs(localColDefs);
                }
              }
            } else {
              if (count == 1) {
                resolve(response.data.payload.entries[0]);
              } else {
                displayNotifications(this, [
                  {
                    type: "toast",
                    class: "is-danger",
                    text: `"✔ could not load data for id ${id}`
                  }
                ]);
              }
            }
          })
          .catch(error => {
            console.error(error);
            this.isLoading = false;
            handleErrorsFromResponse(this, error.response);
            if (record_id !== null) {
              reject(error.response);
            }
          });
      });
      if (record_id !== null) {
        return promise;
      }
    },
    mergeColDefs: function(colDefsFromConf, colDefsFromReq) {
      console.log("#log 4031", colDefsFromConf, colDefsFromReq);
      const coldefs = R.pipe(
        R.reduce((accFromConf, coldefFromReq) => {
          const foundColdefFromConf = R.find(
            R.propEq("field", coldefFromReq.field)
          )(accFromConf);
          if (foundColdefFromConf) {
            //when field from req already in conf, then find and change in accFromConf
            return R.map(coldefFromConf => {
              if (coldefFromConf.field == coldefFromReq.field) {
                return { ...coldefFromReq, ...coldefFromConf };
              } else {
                return coldefFromConf;
              }
            })(accFromConf);
            return [...accFromConf];
          } else {
            //when field from req not in conf, then add as hidden field
            return [...accFromConf, { ...coldefFromReq, hide: true }];
          }
        }, colDefsFromConf)
      )(colDefsFromReq);
      console.log("#log 3008", coldefs);
      return coldefs;
    },
    getRows: function(agParams) {
      console.error("#getRows DEPRECATED !", agParams, this.listparams);
      this.getItemsStarted = true;
      axios
        .post(`/api/ag-grid/rows/${this.table}`, agParams, {
          params: this.listparams
        })
        .then(response => {
          console.log(
            "#getRows got response",
            array_get(response, "data.payload.entries", []).length
          );
          if (response.data.payload.col_defs) {
            let localColDefs = response.data.payload.localColDefs;
            if (this.columnDefs) {
              localColDefs = this.mergeColDefs(this.columnDefs, localColDefs);
            }
            this.setColDefs(localColDefs);
          }
          setTimeout(() => {
            console.log(
              "#callbaack",
              response.data.payload.entries,
              response.data.payload.total_entries
            );
            var lastRow = -1;
            if (response.data.payload.total_entries <= agParams.endRow) {
              lastRow = response.data.payload.total_entries;
            }

            agParams.successCallback(response.data.payload.entries, lastRow);
          }, 1000);
        });
    },
    onCellEditingStarted: function(arg1, arg2) {
      console.log("#log 8472 onCellEditingStarted", arg1, arg2);
    },
    onRowDataChanged: function(params) {
      this.baseOnRowDataChanged(params);
    },
    baseOnRowDataChanged: function(params) {
      this.resizeColumns(params);
    },
    onFilterChanged: function(params) {
      this.baseOnFilterChanged(params);
    },
    onColumnRowGroupChanged: function() {
      this.setIsGrouped();
    },
    onPaginationChanged: function() {
      console.log("onPaginationPageLoaded");
      if (this.gridApi) {
        this.paginationLastPageFound = this.gridApi.paginationIsLastPageFound();
        // this.paginationPageSize = this.gridApi.paginationGetPageSize();
        this.paginationCurrentPage =
          this.gridApi.paginationGetCurrentPage() + 1;
        this.paginationTotalPages = this.gridApi.paginationGetTotalPages();
      }
    },
    baseOnFilterChanged: function(params) {},
    resizeColumns: function(params) {
      console.log("#autosize", null);
      this.gridColumnApi.autoSizeAllColumns();
    },
    baseBeforeMount: function() {
      if (!this.table) {
        this.table = array_get(this.listparams, "table", "no_table");
      }
      this.gridOptions = this.createGridOptions();
      this.statusBar = {
        statusPanels: [{ statusPanel: "agAggregationComponent" }]
      };

      this.gridOptions.alignedGrids.push(this.bottomGridOptions);
      this.bottomGridOptions.alignedGrids.push(this.gridOptions);
    },
    getColSum: function(fieldname, rows) {
      return R.reduce((acc, row) => acc + row[fieldname], 0, rows);
    },
    getTotalsDataFromRows: function(rows) {
      const totals = R.reduce(
        (acc, col) => {
          let value = "";
          if (col.type == "numericColumn") {
            value = this.getColSum(col.field, rows);
          }
          return { ...acc, [col.field]: value };
        },
        {},
        this.columnDefs
      );
      return [totals];
    }
  },
  watch: {
    rowData: function(newval) {
      this.totalsData = this.getTotalsDataFromRows(newval);
    }
  },
  mounted: function() {
    this.kw = array_get(this.listparams, "kw");
    this.gridApi = this.gridOptions.api;
    this.gridColumnApi = this.gridOptions.columnApi;

    console.log("#log", this.gridOptions);
    if (
      !this.getItemsStarted &&
      array_get(this.gridOptions, "rowModelType", "clientSide") == "clientSide"
    ) {
      this.getItems();
    }
  },
  beforeMount: function() {
    this.baseBeforeMount();
  }
};
