<template>

  <div class="ag-input-wrapper">
    <input
      ref="inputfield"
      v-model="editValue"
      class="ag-cell-edit-input"
      v-on:keyup.enter="focusOut"
    >

  </div>

</template>

<script>
import { format as date_format, parseISO as date_parse_iso } from "date-fns";

export default {
  name: "GermanDateEditor",
  data() {
    return {
      value: "",
      initialValue: "",
      editValue: "",
      colvalues: []
    };
  },
  beforeMount() {
    this.initialValue = this.params.value;
    this.value = this.initialValue;
    this.updateEditValue();
  },
  mounted() {
    this.$nextTick(() => {
      this.focusIfSelected();
      this.setupEnterHandling();
    });
  },
  methods: {
    setupEnterHandling: function() {
      var KEY_ENTER = 13;

      this.$refs.inputfield.addEventListener("keydown", event => {
        var keyCode = event.keyCode;
        if (keyCode === KEY_ENTER) {
          console.log("#log 9293ENTER", this.params.column.colId);
          this.focusOut();
          // event.stopPropagation();
        }
      });
    },
    updateEditValue: function() {
      this.editValue = this.$options.filters.german_date(this.value);
    },
    focusIn: function() {
      this.$refs.inputfield.focus();
      this.$refs.inputfield.select();
      return true;
    },
    focusOut: function() {
      console.log("#log 4402", "focusout");
      this.parseValue();
    },
    parseValue: function() {
      this.value = this.germanDateParser(this.editValue);
      this.updateEditValue();
    },
    germanDateParser: function(datestr) {
      let ret = "";
      if (datestr) {
        if (datestr.indexOf(".") < 1) {
          datestr =
            datestr.substr(0, 2) +
            "." +
            datestr.substr(2, 2) +
            "." +
            datestr.substr(4);
        }
        let date_arr = datestr.split("."),
          day = parseInt(date_arr[0], 10),
          month_str = date_arr[1],
          year = parseInt(date_arr[2], 10),
          month = month_str - 1,
          ok = null;

        if (!year) {
          let today = new Date();
          let aktyear = today.getFullYear();
          year = aktyear;
          let test = new Date(year, month, day);
          // if date would be to old:
          if (test.getTime() < today.getTime() - 3600000 * 24 * 30) {
            year++;
          }
        } else if (year > 0 && year < 100) {
          year = 2000 + year;
        }

        let test = new Date(year, month, day, 12);

        if (
          test.getFullYear() == year &&
          month == test.getMonth() &&
          day == test.getDate()
        ) {
          ok = true;
        } else {
          ok = false;
        }

        if (year > 2050 || year < 2000) {
          ok = false;
        }
        if (ok) {
          ret = test.toISOString().substring(0, 10);
        }
      }
      console.log("#log parseGermandate", datestr, ret);
      return ret;
    },

    focusIfSelected: function() {
      const hasFocus = this.$el.parentElement.classList.contains(
        "ag-cell-focus"
      );

      if (hasFocus) {
        this.focusIn();
      }
    },

    getValue: function() {
      // runs when row-editing ends
      console.log("#germandate get value", this.value);

      return this.value;
    }

    // isCancelBeforeStart() {
    //   return false;
    // },

    // isCancelAfterEnd() {
    //   return false;
    // }
  },
  computed: {}
};
</script>

