<template>

  <div class="ag-input-wrapper">
    <b-autocomplete
      ref="autocompletefield"
      class="ag-cell-edit-input"
      v-model="value"
      :data="filteredColvalues"
      :open-on-focus="true"
      field="name"
      :keep-first="false"
      @select="option => value = option"
    >
      <template slot="empty">No results found</template>
    </b-autocomplete>
  </div>

</template>

<script>
import axios from "axios";

export default {
  name: "DistinctValueChooser",
  data() {
    return {
      value: "",
      colvalues: []
    };
  },
  beforeMount() {
    this.value = this.params.value;
  },
  created() {},
  mounted() {
    this.$nextTick(() => {
      this.fetchData();
      this.focusIfSelected();
    });
    this.blockKeyboardEvents();
  },
  methods: {
    focusIn: function() {
      this.$refs.autocompletefield.focus();
      return true;
    },
    fetchData: function() {
      this.colvalues = this.$parent.$parent.getColvaluesForField(
        this.params.colDef.field
      );
      console.log("#log 9223", this.params.colDef.field, this.colvalues);
    },
    focusIfSelected: function() {
      const hasFocus = this.$el.parentElement.classList.contains(
        "ag-cell-focus"
      );

      if (hasFocus) {
        this.focusIn();
      }
    },
    blockKeyboardEvents: function() {
      var KEY_LEFT = 37;
      var KEY_UP = 38;
      var KEY_RIGHT = 39;
      var KEY_DOWN = 40;
      var KEY_ENTER = 13;

      console.log("#log 1822", this.$refs.autocompletefield);
      this.$refs.autocompletefield.$el.addEventListener("keydown", event => {
        var keyCode = event.keyCode;

        var isNavigationKey =
          keyCode === KEY_LEFT ||
          keyCode === KEY_RIGHT ||
          keyCode === KEY_UP ||
          keyCode === KEY_DOWN;

        if (isNavigationKey) {
          // this stops the grid from receiving the event and executing keyboard navigation
          console.log("#log 5886", this.$refs.autocompletefield.isActive);
          event.stopPropagation();
        }
        // if (keyCode === KEY_ENTER) {
        //   console.log("#log 5886", this.$refs.autocompletefield.isActive);
        //   event.stopPropagation();
        // }
      });
    },

    getValue() {
      console.log("#log 7960 get value", this.value);

      return this.value;
    },

    isCancelBeforeStart() {
      return false;
    },

    isCancelAfterEnd() {
      return false;
    }
    // isPopup: function() {
    //   return true;
    // }
  },
  computed: {
    filteredColvalues: function() {
      let str = this.value;
      if (!str) {
        str = "";
      }
      str = str.toLowerCase();
      return this.colvalues.filter(option => {
        return (
          option &&
          option
            .toString()
            .toLowerCase()
            .indexOf(str) >= 0
        );
      });
    }
  }
};
</script>

