import axios from "axios";
import {
  handleErrorsFromResponse,
  handleNotificationsFromResponse,
  displayNotifications,
  array_get
} from "../utils.js";

export const callApi = (
  { commit, getters, state },
  { component, url, data = {}, method = "POST" }
) => {
  const callUrl = "/api" + url;
  console.log(`➜ ➜  calling API ${method} ${callUrl}`, data);
  return new Promise((resolve, reject) => {
    axios
      .request({ url: callUrl, method, data })
      .then(response => {
        console.log(`← ← API returned ${method} ${callUrl}`, response.data);
        commit("setCurrentProd", response.data.payload);
        handleNotificationsFromResponse(component, response);
        resolve(response.data.valid);
      })
      .catch(error => {
        console.error(`← ←  API failed ${method} ${callUrl}`, error);

        handleErrorsFromResponse(component, error.response);
        reject(error.response);
      });
  });
};
