import Vue from "vue";
import VueRouter from "vue-router";
import GenericList from "./components/GenericList";

Vue.use(VueRouter);

const router = new VueRouter({
  linkActiveClass: "is-active",
  mode: "abstract",
  routes: [{
    path: "/generic",
    component: GenericList,
  }]
});

Vue.router = router;

export default router;
