const root_element = document.getElementById("app");
const R = require("ramda");

let extconf = {
  ...root_element.dataset
};
console.log('#log 2537', extconf);
if (extconf.data) {
  extconf.data = JSON.parse(extconf.data);
}

let isPrimitive = function (o) {
  return typeof o !== "object" || null;
};

let csrfNode = document.head.querySelector('meta[name="csrf-token"]');
let csrf = {};
if (csrfNode) {
  csrf.token = csrfNode.getAttribute('content');
  if (csrfNode.hasAttribute('csrf-param')) csrf.param = csrfNode.getAttribute('csrf-param');
  if (csrfNode.hasAttribute('csrf-header')) csrf.header = csrfNode.getAttribute('csrf-header');
  if (csrfNode.hasAttribute('method-param')) csrf.method = csrfNode.getAttribute('method-param');
} else if (console) {
  console.error('\'meta[name="csrf-token"]\' is missing.Please add it into the page ');
}

const params = R.omit(['route'])(Object.assign({}, R.filter(isPrimitive, extconf)));
console.log('#log 7409', params);
export default {
  debug: true,
  counter: 0,
  extconf: extconf,
  csrf,
  params,
  data: extconf.data,
  currentProd: null
};
