<template>

  <div class="ag-cell-edit-input">
    <select
      ref="inputfield"
      v-model="value"
      class="ag-cell-edit-input"
    >
      <option
        v-for="(item, index) in colvalues"
        :key="index"
        :value="item[0]"
      >{{item[1]}}</option>

    </select>

  </div>

</template>

<script>
export default {
  name: "DropDownEditor",
  data() {
    return {
      value: "",
      initialValue: "",
      colvalues: []
    };
  },
  beforeMount() {
    this.initialValue = this.params.value;
    this.value = this.initialValue;
  },

  mounted() {
    this.$nextTick(() => {
      this.focusIfSelected();
      this.fetchData();
    });
  },
  methods: {
    fetchData: function() {
      console.log(`#log 3447`, this.params.colDef.field);
      this.colvalues = this.$parent.$parent.getColvaluesForField(
        this.params.colDef.field
      );
    },
    focusIn: function() {
      this.$refs.inputfield.focus();
      return true;
    },
    focusOut: function() {
      console.log("#log 4402", "focusout");
      this.parseValue();
    },
    focusIfSelected: function() {
      const hasFocus = this.$el.parentElement.classList.contains(
        "ag-cell-focus"
      );

      if (hasFocus) {
        this.focusIn();
      }
    },

    getValue: function() {
      // runs when row-editing ends
      return this.value;
    }
  }
};
</script>

<style  lang="scss">
.dropdown-editor {
  position: relative;
  select {
    width: 100%;
    height: 100%;
  }
}
</style>
