import Vue from "vue";
import App from "./App.vue";
import Buefy from "buefy";
import "buefy/dist/buefy.css";

import router from "./router.js";

import "ag-grid-enterprise";
// import { LicenseManager } from "ag-grid-enterprise";

import number_format from "number-formatter";
import store from "./store";
import {
  format as date_format,
  parseISO as date_parse_iso
} from "date-fns";

// LicenseManager.setLicenseKey( //   "Evaluation_License-_Not_For_Production_Valid_Until_18_April_2019__MTU1NTU0MjAwMDAwMA==8fd2c70c6df61f5ac31e95fee7a8c13a" // );

Vue.config.productionTip = false;

Vue.use(Buefy, {
  defaultIconPack: "fal"
});

Vue.filter("german_date", (val, format = "dd.MM.yyyy") => {
  if (val) {
    const date = date_parse_iso(val);
    return date_format(date, format);
  }
  return "";
});

Vue.filter("currency", (val, mask = "##,##0.00") => number_format(mask, val));
Vue.filter("percent", (val, mask = "+0,##%") =>
  number_format(mask, val * 100, {
    enforceMaskSign: true
  })
);
Vue.filter("netto", (val, tax = 1.2) => val / tax);

router.replace(store.getters.route);

const AppRoot = Vue.extend(App);
window.vm = new AppRoot({
  el: document.getElementById("app"),
  router,
  store
});

window.number_format = number_format

