
<script>
import BaseList from "./BaseList.vue";

import { array_get } from "../utils.js";

const R = require("ramda");

var Hjson = require("hjson");

export default {
  name: "GenericList",
  extends: BaseList,
  data() {
    return {
      listparams: {},
      rowGroupPanelShow: "onlyWhenGrouping",
      defaultGridOptions: {
        enableCellTextSelection: true
      }
    };
  },
  components: {},
  methods: {
    restoreGridStateFromLocalStorage: function() {
      console.log("SKIP restoreGridStateFromLocalStorage");
    },
    parseHJson: function(str) {
      let obj = null;
      try {
        obj = Hjson.parse(str);
        console.log("#obj:", obj);
        this.error = null;
      } catch (e) {
        this.error = e.message;
      }
      return obj;
    }
  },
  computed: {},
  beforeMount: function() {
    let listparams = {};
    R.mapObjIndexed((val, key, obj) => {
      // console.log(`#log 8408 map: ${key}=`, val);
      if (R.has(key, this)) {
        // console.log(`#log 8408 set ${key}=${val} `);
        // this[key] = val;
        this.$set(this, key, val);
      } else {
        listparams[key] = val;
      }
    }, this.$store.getters.params);

    this.$set(this, "listparams", R.omit(["coldefs"], listparams));

    if (array_get(this.$store.getters.params, "editable")) {
      this.makeEditable();
    }

    // if (array_get(this.$store.getters.params, "nameOfIdColumn")) {
    //   this.nameOfIdColumn = array_get(
    //     this.$store.getters.params,
    //     "nameOfIdColumn"
    //   );
    // }
    // if (array_get(this.$store.getters.params, "paginationPageSize")) {
    //   this.paginationPageSize = array_get(
    //     this.$store.getters.params,
    //     "paginationPageSize"
    //   );
    // }

    // if (array_get(this.$store.getters.params, "allow_delete")) {
    //   this.makeEditable();
    //   this.allowDelete = array_get(this.$store.getters.params, "allow_delete");
    // }
    // if (array_get(this.$store.getters.params, "allow_add")) {
    //   this.makeEditable();
    //   this.allowAdd = array_get(this.$store.getters.params, "allow_add");
    // }

    this.baseBeforeMount();
    let coldefsAsJson = array_get(this.$store.getters.params, "coldefs");
    if (coldefsAsJson) {
      let coldefs = this.parseHJson(coldefsAsJson);
      this.initialColumnDefs = this.createColumnDefs(coldefs);
      console.log("#log 1286 initialColumnDefs", this.initialColumnDefs);
    }
    coldefsAsJson = array_get(this.$store.getters.params, "extendColdefs");
    if (coldefsAsJson) {
      let coldefs = this.parseHJson(coldefsAsJson);
      this.extendColumnDefs = this.createColumnDefs(coldefs);
      console.log("#log 1286 extendColumnDefs", this.extendColumnDefs);
    }
  }
};
</script>

